import { useEffect, useState } from "react";
import React from "react";
import PopularMovies from "./PopularMovies";

const Welcome = () => {
    const [ searchItem, setSearchItem ] = useState([]);
    const [ searchTerm, setSearchTerm ] = useState("");
    

    
    const SEARCH_API = "https://api.themoviedb.org/3/search/multi?api_key=e0eb34f3bddc89aabdffd507b370db1e&query=";

    const handleOnSubmit = (e) => {
        e.preventDefault();
        
        if(searchTerm) {
        
        fetch(SEARCH_API + searchTerm)
        .then((res) => res.json())
        .then((data) => {
            console.log(data);
            console.log(searchTerm)
            setSearchItem(data.results);
        })
            setSearchTerm("");
        
        }
    }

    const handleOnChange = (e) => {
        console.log(searchTerm)
        setSearchTerm(e.target.value);
    }

    return (
        <>
            <div className="top-container">
                        <div className="welcome-header-container">
                            <h1 className="welcome-h1">Welcome.</h1>
                            <h2 className="welcome-h2">Millions of movies, TV shows and people to discover. Explore Now. </h2>
                        </div>    
                        <form onSubmit={handleOnSubmit}>
                        <input 
                                className="search" 
                                type="search" 
                                placeholder="Search..."
                                value={searchTerm}
                                onChange={handleOnChange}
                            />
                            <button className="submit-button" type="submit">Search</button>
                        </form>
            </div>
            <PopularMovies/>
        </>
    )
}
export default Welcome;