import { useEffect, useState } from "react";
import axios from "axios";

const POPULAR_API = "https://api.themoviedb.org/3/movie/popular?api_key=e0eb34f3bddc89aabdffd507b370db1e&language=en-US&page=1"
const IMG_API = "https://image.tmdb.org/t/p/w1280";


const PopularMovies = () =>{
    const [ movies, setMovies] = useState([]);

    useEffect(() => {
        axios
            .get(POPULAR_API)
            .then((res) => {
                setMovies(res.data.results)
                console.log(res.data);
            })
            .catch((err) => {
                console.log(err);
            })
    },[]);

    return (
        <div className="popular-movies-container">
            <div className="popular-movies-h2">
                <h2>What's Popular</h2>
            </div>
            <div className="movies-container">
                {movies.map((movies, idx) => {
                    return (
                        <div key={idx} className="movie-container">
                            <div className="movie-image">
                                <img src={IMG_API + movies.poster_path} alt={movies.title} />

                            </div>
                            <div className="movie-info">
                                <h4>{movies.title}</h4>
                                <p>{movies.release_date}</p>
                            </div>
                        </div>    
                    )    
                })}

            </div>

        </div>
    )
}
export default PopularMovies;